<template>
  <div>
    <img src="@/assets/img/quicktime.png" class="qtp">
    <el-row class="login">
      <el-col :md="{span: 10, offset: 7}" :sm="{span: 12,offset: 6}" :lg="{span: 8,offset: 8}" :xs="{span: 20,offset: 2}">
      <!-- DOM ref=form -->
      <el-form :model="form" :label-position="labelPosition" ref="form" :rules="rules" label-width="100px">
        <!--  phoneNum -->
        <el-form-item id="phone" label="" @focus="scrollPhone" prop='phoneNum'>
          <el-input v-model.number="form.phoneNum" type="text" placeholder="">
          </el-input>
        </el-form-item>
        <!--  passWord-->
        <el-form-item label="" @focus="scrollPhone" prop='passWord'>
          <el-input v-model="form.passWord" type="password" placeholder="">
          </el-input>
        </el-form-item>
        <!-- radio-groupform-item identity -->
        <el-form-item label="" prop="identity">
          <el-radio-group v-model="form.identity" :size="small" class="radio-group">
            <!-- <el-radio class="radio" label=""></el-radio> -->
            <el-radio class="radio" label=""></el-radio>
            <el-radio class="radio" label=""></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <!--  Loginresetprimary -->
      <el-button type="primary" @click="Login('form')" size="large" style="width:1.5em"></el-button>
      <el-button type="primary" @click="reset('form')" size="large" style="width:1.5em"></el-button>
    </el-col>
    </el-row>
  </div>
</template>

<script>
import { GetQueryString } from '@/util/getUrlParam.js'
export default {
  name: 'Login',
  data() {
    //elementUi
    //rulesvalidator checkPhoneNum
    var checkPhoneNum = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error(''))
        } else {
          if (value.toString().length !== 11) {
            callback(new Error(''))
          } else {
            callback()
          }
        }
      }, 1000)
    }
    return {
      labelPosition: 'right',
      form: {
        phoneNum: '',
        passWord: '',
        identity: ''
      },
      rules: {
        phoneNum: [
          {
            validator: checkPhoneNum,
            trigger: 'blur'
          }
        ],
        passWord: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        identity: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]
      }
    }
  },
  methods: {
    Login(form) {
      var identity = this.form.identity
      let data = {
        'num': this.form.phoneNum,
        'pass': this.form.passWord
      }
      //formvalidate
      this.$refs[form].validate((valid) => {
        if (valid) {
          //console.log(this.form.identity +'   '+identity )
          if (identity === '') {
            this.$axios.post('/api/user/leaderLogin', data).then((response) => {
              if (response.data.length === 1) {
                localStorage.setItem('leaderInfo', response.data[0])
                localStorage.setItem('isLogin','1')
                localStorage.setItem('userName',response.data[0].sphone)
                this.$store.dispatch('changeUserName',response.data[0].sphone)
                this.$store.dispatch('changeLogin',true)
                setTimeout(function() {
                  this.$router.push({
                    path: `/leader`
                  })
                }.bind(this), 500)
              } else {
                this.err()
              }
            })
          } else if (identity === '') {
            this.$axios.post('/api/user/teacherLogin', data).then((response) => {
              console.log(response)
              if(response.data){
                //webStorage
                window.localStorage.setItem('teacherInfo',JSON.stringify(response.data[0]))//JSON
                window.localStorage.setItem('isLogin','1')
                localStorage.setItem('userName',response.data[0].tname)
                this.$store.dispatch('changeUserName',response.data[0].tname)
                this.$store.dispatch('changeLogin',true)
                setTimeout(function(){
                  this.$router.push({
                    path:'/teacher'
                  })
                }.bind(this), 500)
              }else{
                console.log(err)
              }
            })
          } else if (identity === '') {
            let qstarttime = GetQueryString('time')

            this.$axios.post('/api/user/studentLogin', data).then((response) => {
              if (response.data) {
                console.log(response.data[0])
                //webStorage
                window.localStorage.setItem('studentInfo',JSON.stringify(response.data[0]))
                window.localStorage.setItem('isLogin','1')
                localStorage.setItem('userName',response.data[0].sname)
                this.$store.dispatch('changeUserName',response.data[0].sname)
                this.$store.dispatch('changeLogin',true)
                let did = JSON.parse(window.localStorage.getItem('studentInfo')).sno
                setTimeout(function() {
                  this.$router.push({
                    path: `/student?did=`+did+`&time=`+qstarttime
                  })
                }.bind(this), 500)
              } else {
                this.err()//
              }
            })
          }
          // console.log('submit!')
        } else {
          // console.log('error submit!!')
          return false
        }
      })
    },
    reset(form) {
      this.$refs[form].resetFields()
    },
    err() {
      this.$message('')
    },
    scrollPhone(){
      this.$router.push({
        path: `/student/#phone/?did=`+did+`&time=`+qstarttime
      })
    }
    
  },
  components: {
  },
  mounted(){
    //sessionStorage

    if(window.localStorage.getItem('isLogin')){
      if(window.localStorage.getItem('teacherInfo')){
        this.$router.push("/teacher")
      }
      else if(window.localStorage.getItem('studentInfo')){
        let qstarttime = GetQueryString('time')
        let did = JSON.parse(window.localStorage.getItem('studentInfo')).sno
        this.$router.push({
          path: `/student?did=`+did+`&time=`+qstarttime
        })
      }
      else if(window.localStorage.getItem('leaderInfo')){
        this.$router.push("/leader")
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login{
  margin-top: 30px;
}
.qtp{
  width: 200px;
  height: 200px;
}
/*  */
.el-select__input {
    vertical-align: baseline;
}
/* .radio-group{

} */

</style>



