<template>
  <div class="con">
    <el-row class="teacher_con">
      
      <el-col :md="{span:4,offset:4}" :xs="{span:24}" :sm="{span:8}">
        <h6>{{teacherName}}</h6>
          <!--  -->
        <el-menu default-active="1" class="el-menu-vertical-demo" theme="dark">
          <el-menu-item index="1" @click="classinfo">
            <el-icon class="h-icon-users"></el-icon>
          </el-menu-item>

          <el-menu-item index="2" @click="changeTabs">
            <el-icon class="h-icon-filter"></el-icon>
          </el-menu-item>
          <el-menu-item index="3" @click="logout">
            <el-icon class="h-icon-tip_error"></el-icon>
          </el-menu-item>
        </el-menu>
      </el-col>
      <!-- displayblocknone -->
      <el-col  :md="{span: 12}" :xs="{span:24}" :sm="{span:16}">
        
          <!-- count tab tab-->
        <div ref="count" style="display:block">
          <el-tabs type="border-card">
            <!-- tab1 -->
            <el-tab-pane label="">

              <el-select v-model="listVaule" placeholder="" @change="select">
                <el-option v-for="item in list" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            <!-- qrcodejs -->
              <div class="qr_img">
                <svg id="svg" width="300" height="300">
                  <g id="qrcode" ref="qrcode" />
                </svg>
              </div>
            <!--  -->
              <el-button type="info" @click="zoom(1.1)"></el-button>
              <el-button type="info" @click="zoom(0.9)"></el-button>
            </el-tab-pane>
            <!-- tab2 -->
            <el-tab-pane label="">
              <el-button type="info" icon="h-icon-search" :radius="true" class="searchBtn" @click="queryRecord"></el-button>
              <!--  -->
              <div style="margin-top:10px">
                <el-table :data="recordArr"  tooltip-effect="dark" border='false' style="width: 100%">
                  <el-table-column prop="sname" label="" width="80">
                  </el-table-column>
                  <el-table-column prop="ssex" label="" width="80">
                  </el-table-column>
                  <el-table-column prop="sphone" label="" width="150">
                  </el-table-column>
                  <el-table-column prop="qdate" label="" width="230">
                  </el-table-column>
                  <el-table-column prop="qteachermsg" label="">
                  </el-table-column>
                </el-table>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
        <!--  tab manage-->
        <div style="display:none" ref='manage' class="manageBox">
          <p></p>
          <el-input placeholder="" v-model="qrUrl" disabled>
            <template slot="prepend"><span class='fontBorder'>http://</span></template>
          </el-input>
          <div class="select">
            <el-select v-model="startTime" placeholder="">
              <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
//
import QRCode from '@/util/qrcode'
export default {
    data(){
      return{
        teacherName: '',
        disabled: true,
        qrUrl: 'http://192.168.191.1:8080',//ip
        scale: 1,//
        list: [],
        listVaule: '',//
        options: [//
          {
            value:'07:00',
            label:'07:00',
          },
          {
            value:'08:00',
            label:'08:00',
          },
          {
            value:'09:00',
            label:'09:00',
          },
          {
            value:'10:00',
            label:'10:00',
          },
          {
            value:'11:00',
            label:'11:00',
          },
          {
            value:'12:00',
            label:'12:00',
          },
          {
            value:'13:00',
            label:'13:00',
          },
          {
            value:'14:00',
            label:'14:00',
          },
          {
            value:'15:00',
            label:'15:00',
          },
          {
            value:'16:00',
            label:'16:00',
          },
          {
            value:'17:00',
            label:'17:00',
          },
          {
            value:'18:00',
            label:'18:00',
          }
        ],//
        startTime:'',
        recordArr:[],
      }
    },
    created(){//pushlocalStorage
      if(!window.localStorage.getItem('teacherInfo')){
        this.$router.push('/')
      }else{
        this.teacherName = JSON.parse(window.localStorage.getItem('teacherInfo')).tname
        
        //
        let time = new Date()
        let nowHours = time.getHours()
        let nowMinutes = time.getMinutes()
        if(nowMinutes >= 0){
          nowHours++
        }
        this.startTime = nowHours+':'+'00';
        

        // id
        let data = {'id': JSON.parse(window.localStorage.getItem('teacherInfo')).tid}
        //console.log(data)
        this.$axios.post('/api/class/teacherClass',data)
        .then((res)=>{
          //console.log(res.data)
          var listObj = {}
          for (let i = 0; i < res.data.length; i++) {
            listObj.label = res.data[i].did + '' + res.data[i].dnjname + '' + res.data[i].dzyname + '' + res.data[i].dbjname + ''
            listObj.value = res.data[i].did + '' + res.data[i].dnjname + '' + res.data[i].dzyname + '' + res.data[i].dbjname + ''
            if (this.list.length === 0) {
              this.list.push(listObj)
            }
          }
        }).catch((err)=>{
          console.log(err)
        })
        
      }
    },
    methods:{
      classinfo(){
        this.$refs.count.style.display = 'block'
        this.$refs.manage.style.display = 'none'
      },
      //tab1tab2
      changeTabs(){
        //
        this.$refs.count.style.display = 'none'
        this.$refs.manage.style.display = 'block'
      },
      //
      //SVGScalable Vector Graphics
      createQr(cid,url,time){
        const qrcode = new QRCode(document.querySelector('#qrcode'), {
          //text: 'your content',
          width: 100,
          height: 100,
          colorDark: '#000000',
          colorLight: '#ffffff',
          correctLevel: QRCode.CorrectLevel.H, //
          useSVG: true
        })
          qrcode.makeCode(url+'?did='+cid+'&time='+time)
      },
      // 
      zoom(num) {
        var svg = document.querySelector('#svg')
        this.scale = this.scale * num
        if (this.scale < 0.5 || this.scale > 1.1) {
          return
        } else {
          svg.setAttribute('transform', 'scale(' + this.scale + ')')
        }
      },
      //qrcode
      select(){
        let cid = this.listVaule.slice(0,2)//
        if(this.startTime){
          this.$options.methods.createQr(cid,this.qrUrl,this.startTime)// methods this.$options.methods.()
        }
      },
      //
      // 
      queryRecord() {
        let data = {
          'classno': 37
        }
        this.$axios.post('/api/record/recordQuery', data).then((response) => {
          if (response.data) {
            console.log(response.data)
            this.recordArr = response.data
            // this.recordArr = response.data.slice(0)
          } else {
            this.err()
          }
        })
      },
      //  
      logout() {
        //storagevuexislogin
        localStorage.removeItem('teacherInfo')
        localStorage.removeItem('isLogin')
        localStorage.removeItem('userName')
        this.$store.dispatch('changeLogin',false)
        this.$store.dispatch('changeUserName','')
        this.$router.push('/login');
      }
    }
}
</script>
<style scoped>
.con{
    font-size: 2em;
}
.qr_img {
  margin: 0 auto;
  text-align: center;
  margin-top: 20px;
}
.searchBtn{
    float: left;
}
.manageBox{
  padding-left: 1em;
  padding-top: 1em;
  padding-right: 2em;
  background-color: cadetblue;
  height: 10em;
  overflow: hidden;
  border-radius: 4%;
}
</style>



