
//mainjsusevuex
//store.state
//store.commit
//
export default {
    //state
    state:{
        username:'',
        isLogin:false,
    },
    //mutation
    mutations:{
        cName(state,uname){
            localStorage.setItem('userName',uname)
            state.username = uname
        },
        cLogin(state,flag){
            localStorage.setItem('isLogin',flag)
            state.isLogin = flag
        }
    },
    //action dispatchmutations commit
    actions:{
        changeUserName(store,uname){
            store.commit('cName',uname)
        },
        changeLogin(store,flag){
            store.commit('cLogin',flag)
        }
    },
    //getters state
    getters:{
        getUserName(state){
            if(!state.username){
                return state.username = JSON.parse(localStorage.getItem('userName'))
            }else{
                return state.username
            }
        },
        getLoginStatus(state){
            if(!state.isLogin){
                return state.isLogin = JSON.parse(localStorage.getItem('isLogin'))
            }else{
                return state.isLogin
            }
        }
    }
}



