<template>
    <div>
        <el-row>
            <el-col :sm="{span:16,offset:4}" :xs="{span:24}">
                <el-carousel :interval="4000" type="card" height="150px">
                    <el-carousel-item v-for="item in images" :key="item" :label="item.alt">
                        <a :href="item.href">
                            <img :src="item.src" class="carousel_img" :alt="item.alt" width="150px" height="150px">
                        </a>
                    </el-carousel-item>
                </el-carousel>
            </el-col>
        </el-row>

        <el-row class="student_btns">
            <h4>{{ sname }}</h4>
        </el-row>
        
        <el-row class="student_btns">
            <el-button class="diybtn" type="success" size="large" @click="qd"></el-button>
            <el-button class="diybtn" type="primary" size="large"></el-button>
            <el-button class="diybtn" type="primary" size="large" @click="toUserCenter"></el-button>
            <el-button class="diybtn" type="primary" size="large" @click="toUserCenter"></el-button>
            <el-button class="diybtn" type="primary" size="large" @click="logout"></el-button>
        </el-row>

        
    </div>
</template>
<script>
import { GetQueryString } from '@/util/getUrlParam.js'//hash
import { toDateTime } from '@/util/toDateTime.js'//

export default {
    data() {
        return{
            sname: '',
            images: [],
            studentno: ''
        }
    },
    methods:{
    //cid 
    //
    //mysql datetimeJavaScript
    //datetime text datetime
    qd() {
      let data = {
        'studentno': '',//
        'qdate': '',//
        'qtime': '',// 
        'qstarttime': '',//
        'qteachermsg': '',//--------------
      }
      var nowDate = new Date()//
      let sno = JSON.parse(window.localStorage.getItem('studentInfo')).sno //webStorageid
      let qstarttime = GetQueryString('time')// hashtime time

      if (qstarttime != null && qstarttime.toString().length > 1) {
        //
        data.studentno = sno
        data.qdate = toDateTime(nowDate) // --------------------------------------toDateTime
        data.qstarttime = qstarttime
        data.qtime = nowDate.getHours() + ':' + nowDate.getMinutes() + ':' + nowDate.getSeconds()
        //data.qteachermsg 
        let qstartHour = qstarttime.substr(0,2)
        let qstartMinutes = qstarttime.substr(3,2)
        if(nowDate.getHours() < qstartHour){
            data.qteachermsg=''
        }
        else if(nowDate.getHours() >= qstartHour)
        {
            if(nowDate.getMinutes()> qstartMinutes){
                let lateTime = nowDate.getMinutes() - qstartMinutes
                data.qteachermsg = ''+lateTime+''
            }else{
                data.qteachermsg = ''
            }
        }
        console.log(data)

        this.$axios.post('/api/record/recordQd', data).then((response) => {
          console.log(response)
          if (response.statusText === 'OK') {
            this.$message({
              type: 'success',
              message: ''
            })
          } else {
            this.$message.error('')
            this.err()
          }
        })
      }
    },
        toUserCenter(){
            this.$router.push("/userCenter")
        },
        logout(){
            localStorage.removeItem('studentInfo')
            localStorage.removeItem('isLogin')
            localStorage.removeItem('userName')
            this.$store.dispatch('changeLogin',false)
            this.$store.dispatch('changeUserName','')
            this.$router.push('/login');
        }
    },
    created(){
        this.$axios.get('http://192.168.191.1:8080/static/partmock/carouse.json')
        .then((result) => {
            for(let i of result.data.images)
            this.images.push(i)
        }).catch((err) => {
            console.log(err)
        });
        this.sname = JSON.parse(window.localStorage.getItem("studentInfo")).sname
    }
}
</script>
<style scoped>
.carousel_student{
    margin: 0 auto;
    margin-top: 120px;
    width: 60%;
}
.student_btns{
    margin-top: 30px;
}
.diybtn{
    margin-top: 0.5em;
    border-radius: 2%;
}
</style>


